* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#root {
  background-color: #f0f0f0;
}

.link-name {
  text-decoration: none;
  color: white;
  font-size: 1.3rem;
}

.link-name:hover {
  cursor: pointer;
  color: #e3e3e3;
}

